/* Hide scrollbar for Chrome, Safari, and Opera */
::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for Firefox */
html {
  scrollbar-width: none;
  overflow: -moz-scrollbars-none;
}

/* Hide scrollbar for IE and Edge */
 body {
  -ms-overflow-style: none;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


@keyframes fadeOut {
  0%   { opacity: 1; display: block; }
  99%  { opacity: 0; display: block; }
  100% { opacity: 0; display: none; }
}
.App-header {
  
  padding: 10px 20px;  /* reduce padding */
  color: rgb(99, 97, 97);
  text-align: center;  /* center the header text */
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  opacity: 1; /* Start with 90% opacity or any desired value */
  animation: fadeOut 5s forwards 1s; /* Start fade-out after 3 seconds, fade out over 1 second */
  pointer-events: none;
}